*{
    box-sizing: content-box;
    overflow: hidden;
}
body{
    margin: 10px;
    height: 100%;

    
    font-family: 'Comfortaa';
}
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');
.form{
    display: flex;
    overflow:hidden;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-family: 'Ubuntu';
    height: 350px;
    width: 550px;
    background-color: aqua;
    border-radius: 55px;
    line-height: 31px;
    transition-property: background-color, color;
    transition-duration: 3s;
}
input{
    background: skyblue;
}

 /* The switch - the box around the slider */
 .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {display:none;}
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  } 

  .MainContent{
    display: flex;
    box-sizing: border-box;
    height: 1050px;
    background: #666;
  }

  .main-div{
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(43, 42, 42);
    box-sizing: border-box;
    height: 912px;

  }

.Home-content{
    display: grid;
    box-sizing: border-box;
    background: url('../Logos/2005304.jpg');
    height: 1080px;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    grid-auto-rows: minmax(100px, 320px);
}
.Text-content{
    display: grid;
    grid-column: 2;
    grid-row: 2;
    background: -webkit-linear-gradient(-90deg,#F19028, #232323);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 62px;
}